/* Styles specifically for all components */
/* --- BottomNav Component --- */
.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: var(--bottom-nav-height);
  background-color: var(--surface-color);
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 1000;
  max-width: 600px;
  margin: 0 auto;
}

.nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--text-color-light);
  flex-grow: 1;
  padding: var(--padding-sm) 0;
  transition: color 0.2s ease-in-out;
}

.nav-link.active {
  color: var(--primary-color);
}

.nav-link svg {
  width: 24px;
  height: 24px;
  margin-bottom: 4px;
}

.nav-link-label {
  font-size: var(--font-size-xs);
}

/* --- Header Component --- */
.app-header {
  height: var(--header-height);
  background-color: var(--surface-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--padding-md);
  border-bottom: 1px solid var(--border-color);
  position: sticky;
  top: 0;
  z-index: 999;
}

.header-title {
  font-size: var(--font-size-lg);
  font-weight: 600;
  color: var(--text-color);
}

.header-back-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: var(--padding-sm);
  margin-left: -8px; /* Align icon better */
}

.header-back-button svg {
  width: 24px;
  height: 24px;
  color: var(--text-color);
}


/* --- Loader Component --- */
.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loader {
  border: 4px solid var(--border-color);
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* --- Button Component --- */
.btn {
  display: inline-block;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: var(--font-size-md);
  padding: 12px 24px;
  border-radius: var(--border-radius);
  cursor: pointer;
  border: 1px solid transparent;
  text-align: center;
  transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
  width: 100%;
}

.btn-primary {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

.btn-primary:hover:not(:disabled) {
  background-color: var(--primary-color-dark);
  border-color: var(--primary-color-dark);
}

.btn-secondary {
  background-color: transparent;
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-secondary:hover:not(:disabled) {
  background-color: rgba(90, 103, 216, 0.1);
}

.btn:disabled {
  background-color: var(--border-color);
  border-color: var(--border-color);
  color: var(--text-color-light);
  cursor: not-allowed;
}


/* --- Input Component --- */
.input-group {
  margin-bottom: var(--padding-md);
  width: 100%;
}

.input-label {
  display: block;
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--text-color);
  margin-bottom: var(--padding-sm);
}

.input-field {
  width: 100%;
  padding: 12px;
  font-size: var(--font-size-md);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  background-color: var(--background-color);
  color: var(--text-color);
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.input-field:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(90, 103, 216, 0.2);
}

.input-field.error {
    border-color: var(--error-color);
}


/* --- ProductList and ProductCard --- */
.product-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--padding-md);
}

.product-card {
    background-color: var(--surface-color);
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: var(--box-shadow);
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: inherit;
    transition: transform 0.2s ease;
}

.product-card:hover {
    transform: translateY(-5px);
}

.product-card-image-container {
    position: relative;
    width: 100%;
    padding-top: 100%; /* 1:1 Aspect Ratio */
}

.product-card-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product-discount-badge {
    position: absolute;
    top: var(--padding-sm);
    right: var(--padding-sm);
    background-color: var(--secondary-color);
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: var(--font-size-xs);
    font-weight: bold;
}

.product-card-info {
    padding: var(--padding-sm);
}

.product-card-name {
    font-size: var(--font-size-sm);
    font-weight: 500;
    margin: 0 0 4px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.product-card-price {
    display: flex;
    align-items: baseline;
    gap: 8px;
}

.product-discounted-price {
    font-size: var(--font-size-md);
    font-weight: bold;
    color: var(--text-color);
}

.product-original-price {
    font-size: var(--font-size-xs);
    color: var(--text-color-light);
    text-decoration: line-through;
}

/* --- Modal Component --- */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2000;
}

.modal-content {
    background: var(--surface-color);
    padding: var(--padding-lg);
    border-radius: var(--border-radius);
    width: 90%;
    max-width: 400px;
    box-shadow: var(--box-shadow-lg);
}

.modal-content h2 {
    margin-top: 0;
}

.star-rating {
    cursor: pointer;
    font-size: 2rem;
}
.star-rating span {
    color: var(--border-color);
}
.star-rating span.star-filled {
    color: #FBBF24; /* Amber color */
}

.modal-actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 1.5rem;
}

/* --- Notification Toasts --- */
.notification-container {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.notification-toast {
    min-width: 250px;
    padding: var(--padding-md);
    border-radius: var(--border-radius);
    color: white;
    box-shadow: var(--box-shadow-lg);
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: slideInRight 0.3s ease-in-out, fadeOut 0.5s ease-in-out 2.5s forwards;
}

.notification-toast.success {
    background-color: var(--success-color);
}

.notification-toast.error {
    background-color: var(--error-color);
}

.toast-close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    opacity: 0.7;
    margin-left: 1rem;
}

@keyframes slideInRight {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}