/* Styles specifically for all pages */
/* --- General Page Styles --- */
.page-container {
    padding: var(--padding-md);
    animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

/* --- Auth Page (Login/Register) --- */
.auth-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: var(--padding-lg);
}

.auth-form {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.auth-title {
    font-size: var(--font-size-xl);
    font-weight: 700;
    text-align: center;
    margin-bottom: var(--padding-lg);
}

.auth-switch-text {
    text-align: center;
    margin-top: var(--padding-lg);
    font-size: var(--font-size-sm);
}

/* --- Home Page --- */
.home-search-bar {
    margin-bottom: var(--padding-lg);
}

.banner-slider {
    width: 100%;
    height: 180px;
    border-radius: var(--border-radius);
    background-color: var(--border-color);
    margin-bottom: var(--padding-lg);
    overflow: hidden;
}

.banner-slider img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--padding-md);
}

.section-title {
    font-size: var(--font-size-lg);
    font-weight: 600;
}

.view-all-link {
    font-size: var(--font-size-sm);
    font-weight: 500;
    color: var(--primary-color);
}

/* --- Categories Page --- */
.category-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--padding-md);
}

.category-card {
    background-color: var(--surface-color);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
    padding: var(--padding-md);
    text-align: center;
    transition: box-shadow 0.2s ease, transform 0.2s ease;
    cursor: pointer;
}

.category-card:hover {
    transform: translateY(-4px);
    box-shadow: var(--box-shadow);
}

.category-card img {
    width: 80px;
    height: 80px;
    object-fit: contain;
    margin-bottom: var(--padding-sm);
}

.category-card-name {
    font-weight: 500;
    color: var(--text-color);
}


/* --- Account Page --- */
.account-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: var(--padding-lg);
}

.account-info {
    text-align: center;
    margin-bottom: var(--padding-xl);
}

.account-name {
    font-size: var(--font-size-lg);
    font-weight: 600;
    margin-bottom: var(--padding-sm);
}

.account-email {
    font-size: var(--font-size-sm);
    color: var(--text-color-light);
}

.account-menu {
    width: 100%;
}

.account-menu-item {
    display: flex;
    align-items: center;
    padding: var(--padding-md) 0;
    border-bottom: 1px solid var(--border-color);
    cursor: pointer;
    font-size: var(--font-size-md);
}

.account-menu-item:last-child {
    border-bottom: none;
}


/* --- Product Detail Page --- */
.product-detail-container {
    padding-bottom: 80px; /* Space for Add to Cart button */
}

.product-image-slider {
    width: 100%;
}

.product-main-image {
    width: 100%;
    height: auto;
    max-height: 400px;
    object-fit: contain;
    background-color: #f5f5f5;
}

.product-info-section {
    padding: var(--padding-md);
    background-color: var(--surface-color);
}

.product-detail-name {
    font-size: var(--font-size-xl);
    font-weight: 600;
    margin-bottom: var(--padding-sm);
}

.product-detail-discount-badge {
    background-color: var(--secondary-color);
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: var(--font-size-xs);
    font-weight: bold;
    display: inline-block;
    margin-bottom: var(--padding-md);
}

.product-detail-price-container {
    display: flex;
    align-items: baseline;
    gap: 12px;
    margin-bottom: var(--padding-sm);
}

.product-detail-discounted-price {
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--primary-color);
}

.product-detail-original-price {
    font-size: 1.1rem;
    color: var(--text-color-light);
    text-decoration: line-through;
}

.product-rating-summary {
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--text-color-light);
    font-size: var(--font-size-sm);
    margin-bottom: var(--padding-lg);
}

.product-detail-actions {
    margin-bottom: var(--padding-lg);
}

.product-description {
    margin-bottom: var(--padding-lg);
}

.product-description h3 {
    font-size: var(--font-size-lg);
    border-bottom: 2px solid var(--border-color);
    padding-bottom: var(--padding-sm);
    margin-bottom: var(--padding-md);
}

.product-vendor-info {
    font-size: var(--font-size-sm);
    background-color: var(--background-color);
    padding: var(--padding-md);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.product-reviews-section {
    padding: var(--padding-md);
}

.product-reviews-section h3 {
    font-size: var(--font-size-lg);
    border-bottom: 2px solid var(--border-color);
    padding-bottom: var(--padding-sm);
    margin-bottom: var(--padding-md);
}

.review-item {
    border-bottom: 1px solid var(--border-color);
    padding: var(--padding-md) 0;
}
.review-item:last-child {
    border-bottom: none;
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--padding-sm);
}

.review-user {
    font-weight: 600;
}

.review-text {
    margin: 0 0 var(--padding-sm) 0;
    color: var(--text-color-light);
}

.review-date {
    font-size: var(--font-size-xs);
    color: var(--text-color-light);
}

/* --- Cart Page --- */
.cart-items-container {
    padding: var(--padding-md);
    padding-bottom: 160px; /* Space for summary */
}

.cart-item {
    display: flex;
    align-items: center;
    background: var(--surface-color);
    padding: var(--padding-md);
    border-bottom: 1px solid var(--border-color);
}

.cart-item-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: var(--border-radius);
    margin-right: var(--padding-md);
}

.cart-item-details {
    flex-grow: 1;
}

.cart-item-name {
    font-weight: 600;
    margin: 0 0 4px;
}

.cart-item-price {
    color: var(--primary-color);
    font-weight: 500;
    margin-bottom: 8px;
}

.cart-item-quantity-control {
    display: flex;
    align-items: center;
}

.cart-item-quantity-control button {
    width: 28px;
    height: 28px;
    border: 1px solid var(--border-color);
    background: var(--background-color);
    border-radius: 50%;
    cursor: pointer;
    font-size: 1.2rem;
    line-height: 1;
}

.cart-item-quantity-control span {
    padding: 0 12px;
    font-weight: 500;
}

.cart-item-remove {
    background: none;
    border: none;
    font-size: 1.5rem;
    color: var(--error-color);
    cursor: pointer;
    padding: 8px;
}

.cart-summary {
    position: fixed;
    bottom: var(--bottom-nav-height);
    left: 0;
    right: 0;
    max-width: 600px;
    margin: 0 auto;
    background: var(--surface-color);
    padding: var(--padding-md);
    border-top: 1px solid var(--border-color);
    box-shadow: 0 -4px 10px rgba(0,0,0,0.05);
}

.summary-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
    font-size: var(--font-size-md);
}

.summary-row.total {
    font-weight: bold;
    font-size: var(--font-size-lg);
    margin-top: 12px;
    padding-top: 12px;
    border-top: 1px dashed var(--border-color);
}

.empty-cart-container {
    padding: var(--padding-xl);
    text-align: center;
    margin-top: 50px;
}

/* --- Orders and OrderDetails --- */
.order-list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--padding-md);
    background: var(--surface-color);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
    margin-bottom: var(--padding-md);
    text-decoration: none;
    color: inherit;
}

.order-info h4 {
    margin: 0 0 8px;
}
.order-info p {
    margin: 0;
    font-size: var(--font-size-sm);
    color: var(--text-color-light);
}

.order-status {
    padding: 4px 10px;
    border-radius: 12px;
    font-size: var(--font-size-xs);
    font-weight: bold;
    text-transform: capitalize;
}
.order-status.pending { background-color: #FEF3C7; color: #92400E; }
.order-status.delivered { background-color: #D1FAE5; color: #065F46; }
.order-status.shipped { background-color: #DBEAFE; color: #1E40AF; }
.order-status.cancelled { background-color: #FEE2E2; color: #991B1B; }

.order-details-summary {
    background: var(--background-color);
    padding: var(--padding-md);
    border-radius: var(--border-radius);
    margin-bottom: var(--padding-lg);
}
.order-details-summary p { margin: 0 0 8px; }

.order-items-list h3 {
    margin-bottom: var(--padding-md);
}

.order-detail-item {
    display: flex;
    align-items: center;
    padding: var(--padding-md) 0;
    border-bottom: 1px solid var(--border-color);
}
.order-detail-item img {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: var(--border-radius);
    margin-right: var(--padding-md);
}
.order-detail-item .item-info {
    flex-grow: 1;
}
.order-detail-item .item-info p {
    margin: 0;
}

/* --- Category Products Page Filters --- */
.filters-container {
    display: flex;
    gap: var(--padding-md);
    margin-bottom: var(--padding-lg);
    flex-wrap: wrap;
}

.filters-container .input-field {
    flex-grow: 1;
    margin-bottom: 0; /* Override default margin from input group */
    background-color: var(--surface-color);
}