/* Your design system (colors, fonts, etc.) */
:root {
  /* Colors */
  --primary-color: #5A67D8; /* A professional blue/purple */
  --primary-color-dark: #434190;
  --secondary-color: #FF6B6B; /* An accent color for sales/offers */
  
  --text-color: #2D3748; /* Dark Gray */
  --text-color-light: #718096; /* Lighter Gray */
  --background-color: #F7FAFC; /* Very light gray */
  --surface-color: #FFFFFF; /* For cards, modals */
  --border-color: #E2E8F0;
  --success-color: #38A169;
  --error-color: #E53E3E;

  /* Typography */
  --font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  
  --font-size-xl: 1.5rem;   /* 24px */
  --font-size-lg: 1.25rem;  /* 20px */
  --font-size-md: 1rem;     /* 16px */
  --font-size-sm: 0.875rem; /* 14px */
  --font-size-xs: 0.75rem;  /* 12px */

  /* Spacing & Sizing */
  --padding-xl: 2rem;
  --padding-lg: 1.5rem;
  --padding-md: 1rem;
  --padding-sm: 0.5rem;

  /* Borders & Shadows */
  --border-radius: 8px;
  --box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --box-shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);

  /* Layout */
  --bottom-nav-height: 60px;
  --header-height: 56px;
}